import React, { useState } from 'react';
import { Link, Element } from 'react-scroll';
import Gallery from './homework3/components/Gallery/Gallery';
import Blog from './homework3/components/Blog/Blog';
import './App.css';

function App() {
  const [menuOpen, setMenuOpen] = useState(false);

  const toggleMenu = () => {
    setMenuOpen(!menuOpen);
  };

  return (
    <>
      <nav className="navbar">
        <div className="logo">
          <Link to="gallery" smooth={true} duration={500}>Homework</Link>
        </div>
        <button className="hamburger" onClick={toggleMenu}>
          ☰
        </button>
        <div className={`menu ${menuOpen ? 'open' : ''}`}>
          <ul>
            <li>
              <Link to="gallery" smooth={true} duration={500} onClick={toggleMenu}>
                shiftGallery
              </Link>
            </li>
            <li>
              <Link to="blog" smooth={true} duration={500} onClick={toggleMenu}>
                shiftBlog
              </Link>
            </li>
          </ul>
        </div>
        <div className="default-nav">
          <ul>
            <li>
              <Link to="gallery" smooth={true} duration={500}>
                shiftGallery
              </Link>
            </li>
            <li>
              <Link to="blog" smooth={true} duration={500}>
                shiftBlog
              </Link>
            </li>
          </ul>
        </div>
      </nav>
      <Element name="gallery" className="section">
        <Gallery />
      </Element>
      <Element name="blog" className="section">
        <Blog />
      </Element>
    </>
  );
}

export default App;



